/*APP ALL GLOBAL CSS */

* {
    text-align: left;
    box-sizing: border-box;
}

body {
    margin: 0px;
    padding: 0px;
    background: #EFEFEF;
}

@font-face {
    font-family: 'cera-pro-light';
    src: url(../assets/fonts/cera-pro-font/Cera-Pro-Light.ttf);
}

@font-face {
    font-family: 'cera-pro-medium';
    src: url(../assets/fonts/cera-pro-font/Cera-Pro-Medium.ttf);
}

@font-face {
    font-family: 'cera-pro-bold';
    src: url(../assets/fonts/cera-pro-font/Cera-Pro-Bold.ttf);
}

.container {
    max-width: 1140px;
    width: 100%;
    padding-left: -15px;
    padding-right: -15px;
    margin: auto;
    box-sizing: border-box;
}

.grid-main .grid-item {
    background-color: transparent;
    box-shadow: none;
    border-radius: 0px;
    padding: 0px;
}

.medium-text {
    font-family: 'cera-pro-medium' !important;
}

.light-text {
    font-family: 'cera-pro-light' !important;
}

/* start form control css */

.form-group .form-control input {
    width: 100%;
    padding: 17px 16px;
}

.form-group .form-control input::placeholder {
    color: #9E9E9E;
    font-size: 12px;
    line-height: 20px;
    font-family: 'cera-pro-light';
    opacity: 1;
}

fieldset {
    border: 0px;
}

/* end form control css */


