/* #region RESULT PAGE */
.pointer{
    cursor: pointer;
}
.result-container {
    width: 100%;
    max-width: 687px;
    margin: auto;
}

.match-red-header {
    background-color: red;
    border-radius: 20px;
    padding: 22px 25px 18px 25px;
    display: flex;
}

.header-text {
    width: 100%;
}

.header-text div {
    margin: auto;
    max-width: fit-content;
    font-style: normal;
    color: #FFFFFF;
    line-height: 16px;
}

.header-text div:first-child {
    font-family: cera-pro-medium;
    font-weight: 500;
    font-size: 25px;

}

.header-text div:last-child {
    font-family: cera-pro-light;
    font-weight: 300;
    font-size: 15px;
    margin-top: 11px;
}

.match-red-header .left-arrow {
    display: none;
    margin-right: 16px;
}

.result-container .result-header-card {
    background: #FFFFFF;
    border-radius: 20px;
}

.score-content,
.radio-content {
    padding: 23px 36px 22px 36px;
}

.score-content div:first-child,
.score-content .score-box,
.radio-content>div:first-child,
.radio-content div .MuiFormControl-root,
.radio-content div .MuiFormGroup-row,
.radio-content .team-titel,
.flext-between {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.score-content>div:first-child>div,
.radio-content>div:first-child>div {
    background: #454545;
    border-radius: 5px;
    width: max-content;
    padding: 3px 10px;
    font-family: 'cera-pro-medium';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 16px;
    color: #FFFFFF;
}

.score-content .score-box p:first-child {
    border-right: 1px solid #8A8A8A;
}

.score-content .score-box p {
    width: 100%;
    font-family: 'cera-pro-medium';
    font-style: normal;
    font-weight: 500;
    font-size: 36px;
    line-height: 16px;
    color: #000000;
    height: 56px;
    padding-top: 25px;
    margin-bottom: 22px;
}

.score-content .score-box span {
    font-size: 15px;
    line-height: 16px;
    color: #6A6A6A;
}

.score-content .score-box p:last-child {
    text-align: right;
}

.score-content>p:last-child {
    font-family: 'cera-pro-medium';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 16px;
    color: #090909;
    text-align: center;
}

.radio-content>div:first-child p {
    font-family: 'cera-pro-bold';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 16px;
    color: #090909;
}

.radio-content .timestamp {
    text-align: center;
    font-family: 'cera-pro-medium';
    font-style: normal;
    font-weight: 300;
    font-size: 11px;
    line-height: 16px;
    color: #090909;
    margin-top: 8px;
}

.radio-content .question {
    margin-top: 18px;
    text-align: center;
    font-family: 'cera-pro-bold';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #090909;
}

.score-slider-poll span {
    font-family: 'cera-pro-bold';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 10px;
    color: #000000;
}

.radio-content div.MuiFormGroup-row {

    margin-top: 18px;
    padding: 0px 60px;
}

.radio-content div.MuiFormGroup-row div:first-child {
    margin-top: 180px;
    padding: 0px 60px;
}

.score-slider-poll {
    height: 10px !important;
    width: 100%;
    max-width: 457px;
    margin: auto;
}

.poll-container {
    margin: auto;
    width: 100%;
    max-width: 457px;
}

.score-slider-poll .MuiSlider-rail {
    background-color: #E31F29;
    opacity: 1;
}

.score-slider-poll .MuiSlider-thumb {
    display: none;
}

.score-slider-poll .MuiSlider-track {
    border-bottom-right-radius: 0px;
    border-top-right-radius: 0px;
}

.radio-content .team-titel p {
    font-family: 'cera-pro-bold';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 10px;
    color: #000000;
    margin-top: 23px;
}

.radio-content .team-titel p svg {
    font-size: 7px;
    margin-right: 6px;
    vertical-align: bottom;
    margin-bottom: 1.5px;
}

.radio-content .team-titel p:first-child svg {
    color: #000000
}

.radio-content .team-titel p:last-child svg {
    color: #E31F29
}

.radio-content .team-titel {
    width: 80%;
    max-width: 405px;
    margin: auto;
    margin-bottom: 14px;

}

.radio-content .percentage {
    width: 70%;
    max-width: 385px;
    margin: auto;
}

.radio-content .percentage p {
    font-family: 'cera-pro-bold';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 10px;
    color: #000000;
}

.tabs-result button {
    font-family: 'cera-pro-light';
    font-style: normal;
    font-weight: 300;
    font-size: 15px;
    line-height: 10px;
    color: #000000;
}

.tabs-container {
    max-width: 687px;
    width: 100%;
    margin: auto;
    box-sizing: border-box;
}

.matchDetails {
    background: #FFFFFF;
    box-shadow: 5px 5px 4px #EFEFEF;
    border-radius: 20px;
    /* height: 298px; */
}

.matchDetails .title {
    font-family: 'cera-pro-medium';
    font-style: normal;
    font-weight: 500;
    font-size: 17px;
    line-height: 10px;
    color: #000000;
    padding: 18px 0px 7px 16px;
}

.matchDetails-item-container {
    padding: 16px 14px;
    border-bottom: 1px solid #8A8A8A;
}

.matchDetails-item-container p:first-child {
    font-family: 'cera-pro-light';
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 10px;
    color: #000000;
    padding-bottom: 11px;
}

.matchDetails-item-container p:last-child {
    font-family: 'cera-pro-medium';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    color: #000000;
}
.matchDetails div:last-child{
    border: none;
}
/* #endregion */

/* #region ADMIN ADDEDIT FORM */
.admin-addedit-container{
    margin: 15px auto;
    width: 100%;
    padding: 15px;
    max-width: 1228px;
}
.admin-addedit-container .custom-paper{
    width: 100%;
    border-radius: 20px;
    padding-top:  20px ; 
    padding-bottom:  15px ; 
    
}
.admin-addedit-container .form-full-width{
margin: auto;
width: 100%;
}
.ck-editor__editable {
    min-height: 300px!important;
}
/* #endregion */

/* ---------------------------------------------------------------------------------------------------- */
/* #region MEDIA QUERY */
@media screen and (min-width:1536px) {}

@media screen and (min-width:1200px) and (max-width:1535px) {}

@media screen and (min-width:900px) and (max-width:1199px) {}

@media screen and (min-width:600px) and (max-width:899px) {}

@media screen and (max-width:600px) {
    .tabs-container {
        padding-left: 15px;
        padding-right: 15px;
        margin-bottom: 72px;
    }

    .result-container .result-header-card {
        border-radius: 0px 0px 20px 20px;
    }

    .match-red-header {
        border-top-left-radius: 0px;
        border-top-right-radius: 0px;
        padding: 22px 25px 18px 25px;
    }

    .header-text div {
        margin: 0px;
        max-width: fit-content;
        font-style: normal;
        color: #FFFFFF;
        line-height: 16px;
    }

    .match-red-header .left-arrow {
        display: inline-block;
    }

    .radio-content div.MuiFormGroup-row {
        padding: 0px;
    }
}

/* #endregion */